/* Global base styles using dynamic Sanity settings */

:root {
  /* Default fallback values - will be overridden by Sanity settings */
  --color-primary: #2563eb;
  --color-black: #000000;
  --color-white: #ffffff;
  --color-gray-light: #f3f4f6;
  --color-gray: #6b7280;
  --color-gray-dark: #374151;

  --font-primary:
    "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  --font-heading: "Playfair Display", Georgia, serif;

  --font-size-hero: 5rem;
  --font-size-h1: 3rem;
  --font-size-h2: 2.5rem;
  --font-size-h3: 2rem;
  --font-size-base: 1rem;
  --font-size-small: 0.875rem;

  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;
  --spacing-3xl: 64px;

  --container-max-width: 1280px;
  --section-padding: 80px;
  --header-height: 80px;
  --footer-height: 200px;

  --button-border-radius: 8px;
  --button-padding: 12px;
  --card-border-radius: 12px;
  --card-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

/* Base typography using dynamic values */
body {
  font-family: var(--font-primary);
  font-size: var(--font-size-base);
  color: var(--color-black);
  background-color: var(--color-white);
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

/* Headings using dynamic values */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  color: var(--color-black);
  margin: 0 0 var(--spacing-md) 0;
}

h1 {
  font-size: var(--font-size-h1);
}

h2 {
  font-size: var(--font-size-h2);
}

h3 {
  font-size: var(--font-size-h3);
}

.hero-title {
  font-size: var(--font-size-hero);
  font-family: var(--font-heading);
}

/* Container using dynamic max-width */
.container {
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 var(--spacing-lg);
}

/* Section padding using dynamic values */
.section {
  padding: var(--section-padding) 0;
}

/* Button styles using dynamic values */
.button {
  background-color: var(--color-primary);
  color: var(--color-white);
  border: none;
  border-radius: var(--button-border-radius);
  padding: var(--button-padding) calc(var(--button-padding) * 2);
  font-family: var(--font-primary);
  font-size: var(--font-size-base);
  cursor: pointer;
  transition: all 0.2s ease;
}

.button:hover {
  opacity: 0.9;
  transform: translateY(-1px);
}

.button-secondary {
  background-color: transparent;
  color: var(--color-primary);
  border: 2px solid var(--color-primary);
}

.button-secondary:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
}

/* Card styles using dynamic values */
.card {
  background-color: var(--color-white);
  border-radius: var(--card-border-radius);
  box-shadow: var(--card-shadow);
  padding: var(--spacing-xl);
  margin-bottom: var(--spacing-lg);
}

/* Navigation using dynamic values */
.navigation {
  height: var(--header-height);
  background-color: var(--color-white);
  border-bottom: 1px solid var(--color-gray-light);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--spacing-lg);
}

.navigation a {
  color: var(--color-black);
  text-decoration: none;
  margin: 0 var(--spacing-md);
  font-family: var(--font-primary);
  transition: color 0.2s ease;
}

.navigation a:hover {
  color: var(--color-primary);
}

/* Grid system using dynamic spacing */
.grid {
  display: grid;
  gap: var(--spacing-lg);
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
}

.grid-3 {
  grid-template-columns: repeat(3, 1fr);
}

.grid-4 {
  grid-template-columns: repeat(4, 1fr);
}

/* Utility classes using dynamic values */
.text-primary {
  color: var(--color-primary);
}

.text-gray {
  color: var(--color-gray);
}

.text-small {
  font-size: var(--font-size-small);
}

.bg-gray-light {
  background-color: var(--color-gray-light);
}

.p-xs {
  padding: var(--spacing-xs);
}
.p-sm {
  padding: var(--spacing-sm);
}
.p-md {
  padding: var(--spacing-md);
}
.p-lg {
  padding: var(--spacing-lg);
}
.p-xl {
  padding: var(--spacing-xl);
}

.m-xs {
  margin: var(--spacing-xs);
}
.m-sm {
  margin: var(--spacing-sm);
}
.m-md {
  margin: var(--spacing-md);
}
.m-lg {
  margin: var(--spacing-lg);
}
.m-xl {
  margin: var(--spacing-xl);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    padding: 0 var(--spacing-md);
  }

  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }

  .navigation {
    padding: 0 var(--spacing-md);
  }

  .hero-title {
    font-size: calc(var(--font-size-hero) * 0.6);
  }
}

/* Loading state while theme is being applied */
.App:not([data-theme-ready="true"]) {
  opacity: 0.8;
}
